import React, { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import Cookies from "js-cookie";
import { styled, useTheme } from "@mui/material/styles";
import Box from "@mui/material/Box";
import Drawer from "@mui/material/Drawer";
import CssBaseline from "@mui/material/CssBaseline";
import MuiAppBar from "@mui/material/AppBar";
import Toolbar from "@mui/material/Toolbar";
import List from "@mui/material/List";
import Typography from "@mui/material/Typography";
import Divider from "@mui/material/Divider";
import IconButton from "@mui/material/IconButton";
import MenuIcon from "@mui/icons-material/Menu";
import ChevronLeftIcon from "@mui/icons-material/ChevronLeft";
import ListItem from "@mui/material/ListItem";
import ListItemButton from "@mui/material/ListItemButton";
import ListItemIcon from "@mui/material/ListItemIcon";
import ListItemText from "@mui/material/ListItemText";
import InboxIcon from "@mui/icons-material/MoveToInbox";
import Button from "@mui/material/Button";
import Badge from "@mui/material/Badge";
import NotificationsIcon from "@mui/icons-material/Notifications";
import AccountCircle from "@mui/icons-material/AccountCircle";
import AddIcon from "@mui/icons-material/Add";
import dxfParser from "dxf-parser";
import convertDXFToGeoJSON from "../Map/convertDXFToGeoJSON";
import { SimpleTreeView } from "@mui/x-tree-view/SimpleTreeView";
import { TreeItem } from "@mui/x-tree-view/TreeItem";
import ChevronRightIcon from "@mui/icons-material/ChevronRight";
import { GetAllLayers } from "../../api/Axios";

const drawerWidth = 240;
const navItems = [
  {
    name: "Home",
    url: "/",
  },
  {
    name: "Map",
    url: "/map",
  },
  {
    name: "Projects",
    url: "/recent-projects",
  },
];

const AppBar = styled(MuiAppBar, {
  shouldForwardProp: (prop) => prop !== "open",
})(({ theme, open }) => ({
  backgroundColor: "#fff",
  color: "#1976d2",
  transition: theme.transitions.create(["margin", "width"], {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
  }),
  ...(open && {
    width: `calc(100% - ${drawerWidth}px)`,
    marginLeft: `${drawerWidth}px`,
    transition: theme.transitions.create(["margin", "width"], {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
  }),
}));

const DrawerHeader = styled("div")(({ theme }) => ({
  display: "flex",
  alignItems: "center",
  padding: theme.spacing(0, 1),
  ...theme.mixins.toolbar,
  justifyContent: "flex-end",
}));

export default function PersistentDrawerLeft({
  open,
  setOpen,
  setOpenNewLayer,
  setLayer,
  setOpenNewDXFLayer,
  lines,
  setLines,
}) {
  const theme = useTheme();
  const [layerData, setLayerData] = useState([]);
  const [selectedLayerId, setSelectedLayerId] = useState(null);
  const [selectedDXFLayerId, setSelectedDXFLayerId] = useState(null);
  const location = useLocation();
  const currentPath = location.pathname;
  const navigate = useNavigate();
  const project_name = Cookies.get("project_name");

  const handleDrawerOpen = () => {
    setOpen(true);
  };

  const handleDrawerClose = () => {
    setOpen(false);
  };

  const handleAddNewModalOpen = () => {
    setOpenNewLayer(true);
  };

  const handleAddNewDXFModalOpen = () => {
    setOpenNewDXFLayer(true);
  };

  const handleLayerButtonClick = (layer_id, layer_name) => {
    setLayer({ layer_id, layer_name });
    setSelectedLayerId(layer_id);
  };

  const DownloadDXF = async (layer_id, downloadLink) => {
    try {
      console.log("Downloading file from", downloadLink);
      const response = await fetch(downloadLink);
      if (!response.ok) throw new Error("Network response was not ok.");
      const fileText = await response.text();

      const parser = new dxfParser();
      const dxfData = parser.parseSync(fileText);
      const geoJSONData = convertDXFToGeoJSON(dxfData);
      console.log("geoJSONData", geoJSONData.features);

      setLines([...lines, ...geoJSONData.features]);
      setSelectedDXFLayerId(layer_id);
    } catch (error) {
      console.error("Error downloading or parsing file:", error);
    }
  };

  const handleClickNavitem = (path) => {
    navigate(path);
  };

  useEffect(() => {
    if (currentPath === "/map" && Cookies.get("project_id") !== null) {
      GetAllLayers(Cookies.get("project_id"))
        .then((response) => {
          console.log("layer data", response.data);
          setLayerData(response.data.data);
        })
        .catch((error) => console.error("Error fetching geo data:", error));
    } else {
      Cookies.set("project_id", null);
      Cookies.set("project_name", null);
      setLayerData([]);
    }
  }, [currentPath]);

  return (
    <Box sx={{ overflow: "hidden" }}>
      <CssBaseline />
      <AppBar position="fixed" open={open}>
        <Toolbar sx={{ marginTop: "25px" }}>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            onClick={handleDrawerOpen}
            edge="start"
            sx={{
              mr: 2,
              ...(open && { display: "none" }),
            }}
          >
            <MenuIcon />
          </IconButton>
          <Typography variant="h5" noWrap component="div">
            {project_name !== "null" ? project_name : "GIS PROJECTS"}
            {/* PROJECT_101 */}
          </Typography>
          <Box
            sx={{ display: { xs: "none", sm: "block" }, marginLeft: "auto" }}
          >
            {navItems.map((item) => (
              <Button
                key={item.name}
                sx={{ color: "#1976d2" }}
                onClick={() => handleClickNavitem(item.url)}
              >
                {item.name}
              </Button>
            ))}
          </Box>
          <Box sx={{ marginLeft: "auto" }}>
            <IconButton
              size="large"
              aria-label="show 17 new notifications"
              color="inherit"
            >
              <Badge color="error">
                <NotificationsIcon />
              </Badge>
            </IconButton>
            <IconButton
              size="large"
              edge="end"
              aria-label="account of current user"
              color="inherit"
            >
              <AccountCircle />
            </IconButton>
          </Box>
        </Toolbar>
      </AppBar>
      <Drawer
        sx={{
          width: drawerWidth,
          flexShrink: 0,
          "& .MuiDrawer-paper": {
            backgroundColor: "#1976d2",
            color: "#fff",
            width: drawerWidth,
            boxSizing: "border-box",
          },
        }}
        variant="persistent"
        anchor="left"
        open={open}
      >
        <DrawerHeader sx={{ marginTop: "25px" }}>
          <IconButton onClick={handleDrawerClose}>
            {theme.direction === "ltr" ? (
              <ChevronLeftIcon />
            ) : (
              <ChevronRightIcon />
            )}
          </IconButton>
        </DrawerHeader>
        <Divider sx={{ marginBottom: "20px" }} />
        <List>
          <SimpleTreeView
            aria-label="file system navigator"
            sx={{ flexGrow: 1, maxWidth: 400, overflowY: "auto" }}
          >
            <TreeItem itemId="a" label="DXF Layers">
              {layerData
                ?.filter((layer) => layer.layer_type === "dxf")
                .map((layer) => (
                  <ListItem key={layer.id} sx={{ margin: "auto" }}>
                    <ListItemButton
                      className={
                        layer.id === selectedDXFLayerId
                          ? "Mui-focusVisible"
                          : ""
                      }
                      onClick={() => {
                        DownloadDXF(layer.id, layer.download_link);
                      }}
                    >
                      <ListItemIcon>
                        <InboxIcon sx={{ color: "#fff" }} />
                      </ListItemIcon>
                      <ListItemText
                        primaryTypographyProps={{
                          noWrap: true,
                          style: { fontWeight: "normal" },
                        }}
                        primary={layer.layer_name}
                      />
                    </ListItemButton>
                  </ListItem>
                ))}
              <ListItem sx={{ margin: "auto" }}>
                <ListItemButton
                  onClick={() => {
                    handleAddNewDXFModalOpen();
                    handleLayerButtonClick("", "");
                  }}
                >
                  <ListItemIcon>
                    <AddIcon sx={{ color: "#fff" }} />
                  </ListItemIcon>
                  <ListItemText primary="Import DXF File" />
                </ListItemButton>
              </ListItem>
            </TreeItem>
            <TreeItem itemId="b" label="MAIN Layers">
              {layerData
                ?.filter((layer) => layer.layer_type !== "dxf")
                .map((layer) => (
                  <ListItem key={layer.id} sx={{ margin: "auto" }}>
                    <ListItemButton
                      className={
                        layer.id === selectedLayerId ? "Mui-focusVisible" : ""
                      }
                      onClick={() =>
                        handleLayerButtonClick(layer.id, layer.layer_name)
                      }
                    >
                      <ListItemIcon>
                        <InboxIcon sx={{ color: "#fff" }} />
                      </ListItemIcon>
                      <ListItemText primary={layer.layer_name} />
                    </ListItemButton>
                  </ListItem>
                ))}
              <ListItem sx={{ margin: "auto" }}>
                <ListItemButton
                  onClick={() => {
                    handleAddNewModalOpen();
                    handleLayerButtonClick("", "");
                  }}
                >
                  <ListItemIcon>
                    <AddIcon sx={{ color: "#fff" }} />
                  </ListItemIcon>
                  <ListItemText primary="Add New Layer" />
                </ListItemButton>
              </ListItem>
            </TreeItem>
          </SimpleTreeView>
        </List>
      </Drawer>
    </Box>
  );
}
