import React, { useEffect, useRef, useState } from "react";
import Dialog from "@mui/material/Dialog";
import Paper from "@mui/material/Paper";
import Typography from "@mui/material/Typography";
import TextField from "@mui/material/TextField";
import AlertComponent from "../Alerts/AlertComponent";
import DialogActions from "@mui/material/DialogActions";
import Button from "@mui/material/Button";
import { SubmitNewLocation } from "../../api/Axios";

const AddAttributeModal = ({
  openNewAttribute,
  setOpenNewAttribute,
  newLocation,
  setNewLocation,
}) => {
  const [alertOpen, setAlertOpen] = useState(false);
  const [alertMessage, setAlertMessage] = useState("");
  const [alertStatus, setAlertStatus] = useState("");
  const isFirstRender = useRef(true);

  const handleClose = () => {
    setOpenNewAttribute(false);
  };

  useEffect(() => {
    if (isFirstRender.current) {
      isFirstRender.current = false;
      return;
    }
    if (newLocation && newLocation.lat) {
      setOpenNewAttribute(true);
    }
  });

  const handleChange = (key, e) => {
    setNewLocation((prevState) => ({ ...prevState, [key]: e.target.value }));
  };

  const handleCancelCreate = () => {
    setNewLocation(prevState => ({ ...prevState, lat: '', lng: '' }));
    handleClose();
  };

  const handleSubmit = () => {
    const isValid = Object.entries(newLocation).every(
      ([key, value]) => key === "id" || value !== null
    );    

    if (isValid) {
      SubmitNewLocation(newLocation)
        .then((response) => {
          handleClose();
          setAlertMessage(response.data.message);
          setAlertStatus(response.data.status);
          setAlertOpen(true);
          setNewLocation(null);
        })
        .catch((error) => {
          console.error("Error submitting new location:", error);
          handleClose();
          setAlertMessage(error.message);
          setAlertStatus(false);
          setAlertOpen(true);
        });
      handleClose();
    } else {
      handleClose();
      setAlertMessage("Please fill all the fields");
      setAlertStatus(false);
      setAlertOpen(true);
    }
    console.log("newLocation", isValid);
    // handleClose();
  };

  return (
    <div>
      <React.Fragment>
        <Dialog
          open={openNewAttribute}
          onClose={handleClose}
          PaperProps={{
            style: {
              width: "25%",
              color: "#1976d2",
              position: "absolute",
              right: "0",
              top: "0",
              outline: "none",
              backgroundColor: "white",
            },
          }}
        >
          <Paper component="form" style={{ width: "100%", color: "#1976d2" }}>
            <div
              style={{
                display: "flex",
                justifyContent: "space-between",
                alignItems: "flex-start",
              }}
            >
              <Typography
                variant="h6"
                sx={{ m: 0, p: 2 }}
                id="customized-dialog-title"
              >
                {newLocation && Object?.keys(newLocation)[1]}
              </Typography>
              {/* <IconButton
                aria-label="close"
                sx={{
                  // position: "absolute",
                  right: 8,
                  top: 8,
                  color: (theme) => theme.palette.grey[1000],
                }}
                onClick={handleClose}
              >
                <CloseIcon />
              </IconButton> */}
            </div>
            <Paper style={{ padding: "20px" }}>
              {newLocation &&
                Object.keys(newLocation).map((key) =>
                  key === "id" ? null : (
                    <TextField
                      key={key}
                      label={key.includes("layer_") ? "name" : key}
                      // label={key}
                      value={newLocation[key] || ""}
                      margin="normal"
                      variant="outlined"
                      sx={{
                        width: "100%",
                      }}
                      disabled={key === "lat" || key === "lng"}
                      onChange={(e) => handleChange(key, e)}
                    />
                  )
                )}
            </Paper>
          </Paper>
          <DialogActions>
            <Button
              variant="contained"
              onClick={handleSubmit}
              // disabled={!createButton}
            >
              Create
            </Button>
            <Button onClick={handleCancelCreate}>Cancel</Button>
          </DialogActions>
        </Dialog>
        {alertOpen ? (
          <AlertComponent
            alertOpen={alertOpen}
            setAlertOpen={setAlertOpen}
            message={alertMessage}
            alertStatus={alertStatus}
          />
        ) : null}
      </React.Fragment>
    </div>
  );
};

export default AddAttributeModal;
