import React, { useState, useEffect } from "react";
import { MapContainer, TileLayer, Marker, Polyline } from "react-leaflet";
import MapPointTool from "./Tools/MapPointTool";
import { GetAllLayerData } from "../../api/Axios";
import "proj4leaflet";

function DisplayMap({ layer, lines, setNewLocation }) {
  const [clickedLocation, setClickedLocation] = useState([]);
  const [locations, setLocations] = useState([]);

  useEffect(() => {
    setLocations([]);
    let layerName = layer.layer_name;
    if (layerName === "") return;
    GetAllLayerData(layerName)
      .then((response) => {
        setLocations(response.data.data);
        setNewLocation(response.data.prebuiltEmptyResultObject);
        console.log(
          "response prebuiltEmptyResultObject",
          response.data.prebuiltEmptyResultObject
        );
        console.log("response data mathara", locations);
      })
      .catch((error) => console.error("Error fetching geo data:", error));
  }, [layer.layer_name]);

  return (
    <div>
      <MapContainer
        center={[7.8731, 80.7718]}
        zoom={8}
        style={{ width: "100%", height: `100vh` }}
      >       
        <TileLayer
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
          attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
          zIndex={0}
        />
        <TileLayer
          url="https://richmond.visigeo.com/rgb/{z}/{x}/{y}.png"
          attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
        />        
        {locations.length > 0
          ? locations.map((location) => (
              <Marker key={location.id} position={[location.lat, location.lng]}></Marker>
            ))
          : null}
        {lines.length > 0
          ? lines.map((line, index) => (
              <Polyline
                key={index}
                positions={line.geometry.coordinates.map((coord) => [
                  coord[1],
                  coord[0],
                ])}
              />
            ))
          : null}
        {clickedLocation.length === 0 ? null : (
          <Marker
            position={[clickedLocation.lat, clickedLocation.lng]}
          ></Marker>
        )}
        <MapPointTool
          setClickedLocation={setClickedLocation}
          setNewLocation={setNewLocation}
        />
      </MapContainer>
    </div>
  );
}

export default DisplayMap;
