import React, { useState } from "react";
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";
import HomePage from "./Pages/HomePage";
import MapPage from "./Pages/MapPage";
import Navbar from "./Components/Layout/NavbarComponent";
import Layout from "./Components/Layout/Layout";
import RecentProjects from "./Pages/RecentProjects";
import AddNewProject from "./Components/AddNewProject/AddNewProject";
import AddNewLayerModal from "./Components/Modals/AddNewLayerModal";
import AddAttributeModal from "./Components/Modals/AddAttributeModal";
import AddNewDXFModal from "./Components/Modals/AddNewDXFModal";
import axios from 'axios';
import { getAPIBaseURL } from './utils/commonUtils';
axios.defaults.baseURL = getAPIBaseURL();

function App() {
  const [open, setOpen] = useState(false);
  const [openNewLayer, setOpenNewLayer] = useState(false);
  const [openNewAttribute, setOpenNewAttribute] = useState(false);
  const [layer, setLayer] = useState({    
    layer_id : "",
    layer_name : "",    
});
const [newLocation, setNewLocation] = useState(null);
const [layerData, setLayerData] = useState([]);
const [openNewDXFLayer, setOpenNewDXFLayer] = useState(false);
const [lines, setLines] = useState([]);

console.log("newLocation", newLocation);
  return (
    <div className="App">
      <Router>
        <Navbar
          open={open}
          setOpen={setOpen}
          setOpenNewLayer={setOpenNewLayer}
          setLayer={setLayer}
          setOpenNewDXFLayer={setOpenNewDXFLayer}
          layerData={layerData}
          lines={lines}
          setLines={setLines}
        />
        <Layout open={open}>
          <Routes>
            <Route path="/" element={<HomePage />} />
            <Route path="/map" element={<MapPage layer={layer} lines={lines} setNewLocation={setNewLocation}/>} />
            <Route path="/recent-projects" element={<RecentProjects setLayerData={setLayerData}/>} />
            <Route path="/add-new-project" element={<AddNewProject/>} />
          </Routes>
          {/*Modals*/}
          <AddNewLayerModal
            openNewLayer={openNewLayer}
            setOpenNewLayer={setOpenNewLayer}
          />
          <AddAttributeModal
            openNewAttribute={openNewAttribute}
            setOpenNewAttribute={setOpenNewAttribute}
            newLocation={newLocation}
            setNewLocation={setNewLocation}
          />
          <AddNewDXFModal
            openNewDXFLayer={openNewDXFLayer}
            setOpenNewDXFLayer={setOpenNewDXFLayer}
            lines={lines}
            setLines={setLines}
          />
        </Layout>
      </Router>
    </div>
  );
}

export default App;
