import React, { useState } from "react";
import AlertComponent from "../Alerts/AlertComponent";
import Button from "@mui/material/Button";
import TextField from "@mui/material/TextField";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogTitle from "@mui/material/DialogTitle";
import FormControl from "@mui/material/FormControl";
import FormLabel from "@mui/material/FormLabel";
import dxfParser from "dxf-parser";
import CloudUploadIcon from "@mui/icons-material/CloudUpload";
import Divider from "@mui/material/Divider";
import Select from "@mui/material/Select";
import MenuItem from "@mui/material/MenuItem";
import convertDXFToGeoJSON from "../Map/convertDXFToGeoJSON";
import { CreateDXFLayer } from "../../api/Axios";
import Cookies from "js-cookie";

const AddNewLayerModal = ({
  openNewDXFLayer,
  setOpenNewDXFLayer,
  lines,
  setLines,
}) => {
  const [formData, setFormData] = useState({
    layer_name_dxf: "",
    project_id: "",
    projection_type: "",
  });

  const [alertOpen, setAlertOpen] = useState(false);
  const [alertMessage, setAlertMessage] = useState("");
  const [alertStatus, setAlertStatus] = useState("");
  const [dxfFile, setDXFFile] = useState(null);

  const handleClose = () => {
    setOpenNewDXFLayer(false);   
  };

  const handleDXFFileInput = (event) => {
    console.log("query");
    const file = event.target.files[0];
    console.log("file", file);
    setDXFFile(file);
    const reader = new FileReader();

    reader.onload = (e) => {
      const parser = new dxfParser();
      const dxfData = parser.parseSync(e.target.result);
      const geoJSONData = convertDXFToGeoJSON(dxfData);
      console.log("geoJSONData", geoJSONData.features);
      setLines([...lines, ...geoJSONData.features]);
    };

    reader.readAsText(file);
  };

  const handleChange = (event) => {
    const { name, value } = event.target;
    setFormData((prevFormData) => ({ ...prevFormData, [name]: value }));
    console.log("formData", formData);
  };

  const handleSubmit = async () => {
    const project_id = Cookies.get("project_id");    
    
    // const data = {
    //   layerName: formData.layer_name_dxf,
    //   dxfFile: dxfFile,
    //   project_id: project_id,
    //   projection_type: formData.projection_type,
    // };

    if (
      !formData.layer_name_dxf ||
      !dxfFile ||
      !project_id ||
      !formData.projection_type
    ) {
      setAlertOpen(true);
      setAlertMessage("All fields are required");
      setAlertStatus("error");
      return; 
    }
    const data = new FormData();
    data.append('layerName', formData.layer_name_dxf);
    data.append('dxfFile', dxfFile);
    data.append('project_id', project_id);
    data.append('projection_type', formData.projection_type);

    console.log("data", data);
    CreateDXFLayer(data)
      .then((response) => {
        console.log("response", response);
        setAlertOpen(true);
        setAlertMessage("DXF Layer created successfully");
        setAlertStatus("success");
      })
      .catch((error) => {
        console.error("Error creating layer:", error);
        setAlertOpen(true);
        setAlertMessage("Error creating layer");
        setAlertStatus("error");
      });
  };

  return (
    <React.Fragment>
      <Dialog
        open={openNewDXFLayer}
        onClose={handleClose}
        PaperProps={{
          component: "form",
          onSubmit: (event) => {
            event.preventDefault();
            const formData = new FormData(event.currentTarget);
            const formJson = Object.fromEntries(formData.entries());
            const email = formJson.email;
            console.log(email);
            handleClose();
          },
          style: { width: "100%", color: "#1976d2" },
        }}
      >
        <DialogTitle>Add New DXF Layer</DialogTitle>
        <DialogContent>
          <div style={{ display: "flex", alignItems: "flex-end", mt: 2}}>
            <div>
              <FormControl sx={{ m: 1, width: "100%", pr: 1 }}>
                <FormLabel id="layer_name_dxf" sx={{ color: "#1976d2" }}>
                  DXF Layer Name
                </FormLabel>
                <TextField
                  aria-labelledby="layer_name_dxf"
                  id="layer_name_dxf"
                  name="layer_name_dxf"
                  variant="outlined"
                  value={formData.layer_name_dxf}
                  onChange={handleChange}
                />
              </FormControl>
            </div>
            <div style={{ width: "100%"}}>              
              <FormControl sx={{ m: 1, width: "100%", pr: 1}}>
                <FormLabel id="projection_type_label" sx={{ color: "#1976d2" }}>
                  Projection Type
                </FormLabel>
                <Select
                  labelId="projection_type_label"
                  id="projection_type"
                  name="projection_type"
                  value={formData.projection_type}
                  onChange={handleChange}
                >
                  <MenuItem value="">
                    <em>None</em>
                  </MenuItem>
                  <MenuItem key="sld99" value="sld99">
                    SLD99
                  </MenuItem>
                  <MenuItem key="wgs84" value="wgs84">
                    WGS84
                  </MenuItem>
                </Select>
              </FormControl>
            </div>
          </div>
          <Divider />
          <div
            style={{
              m: 1,
              width: "100%",
              display: "flex",
              justifyContent: "center",
              paddingTop: "20px",
            }}
          >
            <Button
              component="label"
              role={undefined}
              variant="contained"
              tabIndex={-1}
              startIcon={<CloudUploadIcon />}
              width="100%"
            >
              Upload DXF File
              <input
                type="file"
                id="dxfFile"
                name="dxfFile"
                onChange={handleDXFFileInput}
                accept=".dxf"
                hidden
              />
            </Button>
          </div>
        </DialogContent>
        <Divider />
        <DialogActions sx={{ m: 2 }}>
          <Button
            variant="contained"
            onClick={handleSubmit}
          >
            Create
          </Button>
          <Button onClick={handleClose}>Cancel</Button>
        </DialogActions>
      </Dialog>
      {alertOpen ? (
        <AlertComponent
          alertOpen={alertOpen}
          setAlertOpen={setAlertOpen}
          message={alertMessage}
          alertStatus={alertStatus}
        />
      ) : null}
    </React.Fragment>
  );
};

export default AddNewLayerModal;
