import { useMapEvents } from "react-leaflet";

const MapPointTool = ({ setClickedLocation, setNewLocation }) => {
  useMapEvents({
    click: (e) => {
      const { lat, lng } = e.latlng;

      // Define the latitude and longitude boundaries of Sri Lanka
      const latBounds = [5.9, 9.9]; // Replace with actual boundaries
      const lngBounds = [79.6, 81.8]; // Replace with actual boundaries

      // Check if clicked coordinates are within the boundaries
      if (
        lat >= latBounds[0] &&
        lat <= latBounds[1] &&
        lng >= lngBounds[0] &&
        lng <= lngBounds[1]
      ) {
        setClickedLocation({ lat, lng });
        setNewLocation(prevState => ({ ...prevState, lat: lat, lng: lng }));
      }
    },
  });
  return null;
};

export default MapPointTool;
