import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import Cookies from "js-cookie";
import { GetAllProjects } from "../../api/Axios";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Typography from "@mui/material/Typography";
import ListItemButton from "@mui/material/ListItemButton";
import { CardActionArea } from "@mui/material";
import ListItemText from "@mui/material/ListItemText";
import AddIcon from "@mui/icons-material/Add";
import ProjectImage from "../../Assets/Images/projects.webp";
import useFetch from "../../utils/request/useFetch";

export default function RecentProjectsComponent({ setLayerData }) {
  const navigate = useNavigate();

  const { data: projects = [], reFetch, loading } = useFetch({
    url: `/project/getallprojects`,
  });
  const projectList = Array.isArray(projects?.data) ? projects.data : [];

  const handleClickProject = (projectId, projectName) => {
    Cookies.set("project_id", projectId, { expires: 7, secure: true });
    Cookies.set("project_name", projectName, { expires: 7, secure: true });
    navigate(`/map`);
  };

  return (
    <div
      style={{
        display: "flex",
        justifyContent: "space-evenly",
        flexWrap: "wrap",
      }}
    >
      <Card
        sx={{
          maxWidth: 345,
          flex: "30%",
          margin: "2%",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <ListItemButton
          sx={{
            height: "100%",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            "&:hover": {
              ".MuiSvgIcon-root": {
                color: "#1976d2",
              },
            },
          }}
          onClick={() => navigate("/add-new-project")}
        >
          <AddIcon
            sx={{
              color: "#1976d2",
              fontSize: "10rem",
              width: "100%",
            }}
          />
          <ListItemText />
        </ListItemButton>
      </Card>
      {projectList?.map((project) => (
            <Card
              key={project.id}
              sx={{ maxWidth: 345, flex: "30%", margin: "2%" }}
            >
              <CardActionArea
                onClick={() => handleClickProject(project.id, project.project_name)}
              >
                <CardMedia
                  component="img"
                  height="140"
                  image={ProjectImage}
                  alt="green iguana"
                />
                <CardContent>
                  <Typography gutterBottom variant="h5" component="div">
                    {project.project_name}
                  </Typography>
                  <Typography variant="body2" color="text.secondary">
                    {project.project_name}
                  </Typography>
                </CardContent>
              </CardActionArea>
            </Card>
          ))}
    </div>
  );
}
