import axios from "axios";

export const api = axios.create({
  baseURL: "https://tm-gis.onrender.com",
  timeout: 30000,
  headers: {
    "Content-type": "application/json",
  },
});

export const fileApi = axios.create({
  baseURL: "https://tm-gis.onrender.com",
  timeout: 30000,
  headers: {
    // "Content-type": "application/json",
    // "Content-type": "multipart/form-data",
  },
});

//Create a Project
export const CreateProject = async (formData) => {
  debugger
  const res = await axios.post(`/project/createproject`, formData);
  return res;
};

//Get All Projects
export const GetAllProjects = async () => {
  const res = await axios.get(`/project/getallprojects`);
  return res;
};

//SubmitLocations
export const SubmitLocations = async (clickedLocations) => {
  const res = await axios.post(`/gis/submitlocations`, clickedLocations);
  return res;
};

export const GetLocations = async () => {
  const res = await axios.get(`/gis/getlocations`);
  return res;
};

export const CreateLayer = async (formData) => {
  const res = await axios.post(`/gis/createlayer`, formData);
  return res;
};

export const GetAllLayers = async (project_id) => {
  const res = await axios.get(`/gis/getlayers/${project_id}`);
  return res;
};

export const GetAllLayerData = async (layerName) => {
  const res = await axios.get(`/gis/getlayerdata?layer_name=${encodeURIComponent(layerName)}`);
  return res;
};

export const SubmitNewLocation = async (newLocation) => {
  const res = await axios.post(`/gis/submitnewlocation`, newLocation);
  return res;
};

export const CreateDXFLayer = async (formData) => {
  const res = await fileApi.post(`/dxf/submitDXFData`, formData);
  return res;
}