import proj4 from "proj4";

const convertDXFToGeoJSON = (dxfData) => {
  const sourceProjection = "+proj=tmerc +lat_0=7.00042152777778 +lon_0=80.7735530833333 +k=0.9999238418 +x_0=500000 +y_0=500000 +ellps=evrst30 +towgs84=-0.293,766.95,87.713,-0.195704,-1.695068,-3.473016,-0.039338 +units=m +no_defs +type=crs";
  const targetProjection = "+proj=longlat +datum=WGS84 +no_defs";
  console.log("dxfData", dxfData);


  const geoJSONFeatures = dxfData.entities
    .map((entity) => {
      if (
        entity.type === "LWPOLYLINE" ||
        entity.type === "POLYLINE" ||
        entity.type === "LINE"
      ) {
        const coordinates = entity.vertices.map((vertex) => {
          const [x, y] = proj4(sourceProjection, targetProjection, [
            vertex.x,
            vertex.y,
          ]);
          return [x, y];
        });
        return {
          type: "Feature",
          geometry: {
            type: "LineString",
            coordinates: coordinates,
          },
          properties: {},
        };
      } else {
        return null;
      }
    })
    .filter(Boolean);

  return {
    type: "FeatureCollection",
    features: geoJSONFeatures,
  };
};

export default convertDXFToGeoJSON;
