import React, { useState, useEffect } from "react";
import AlertComponent from "../Alerts/AlertComponent";
import Button from "@mui/material/Button";
import TextField from "@mui/material/TextField";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogTitle from "@mui/material/DialogTitle";
import FormControl from "@mui/material/FormControl";
import FormGroup from "@mui/material/FormGroup";
import FormLabel from "@mui/material/FormLabel";
import RadioGroup from "@mui/material/RadioGroup";
import FormControlLabel from "@mui/material/FormControlLabel";
import Radio from "@mui/material/Radio";
import AddIcon from "@mui/icons-material/Add";
import ListItemButton from "@mui/material/ListItemButton";
import ListItemIcon from "@mui/material/ListItemIcon";
import ListItemText from "@mui/material/ListItemText";
import MenuItem from "@mui/material/MenuItem";
import Select from "@mui/material/Select";
import { CreateLayer } from "../../api/Axios";
import Cookies from "js-cookie";

const dataTypes = ["String", "Number", "Date", "Float", "Boolean"];

const AddNewLayerModal = ({ openNewLayer, setOpenNewLayer }) => {
  const [formData, setFormData] = useState({
    layerName: "",
    layerType: "",
    fields: [],
  });
  const [fields, setFields] = useState([{ attributeName: "", dataType: "" }]);
  const [fieldCreateButton, setFieldCreateButton] = useState(false);
  const [createButton, setCreateButton] = useState(false);
  const [alertOpen, setAlertOpen] = useState(false);
  const [alertMessage, setAlertMessage] = useState("");
  const [alertStatus, setAlertStatus] = useState("");

  const handleClose = () => {
    setFormData({
      layerName: "",
      layerType: "",
      fields: [],
    });
    setFields([{ attributeName: "", dataType: "" }]);
    setOpenNewLayer(false);
    window.location.reload();
  };

  const handleChange = (event) => {
    const { name, value } = event.target;
    setFormData((prevFormData) => ({ ...prevFormData, [name]: value }));
    console.log("formData", formData);
  };

  const handleAddField = () => {
    setFields([...fields, { attributeName: "", dataType: "" }]);
  };

  const handleFieldChange = (index, fieldKey, value) => {
    setFormData((prevFormData) => {
      const updatedFields = [...fields];
      updatedFields[index] = { ...updatedFields[index], [fieldKey]: value };
      console.log("newFields", prevFormData);
      setFields(updatedFields);
      return { ...prevFormData, fields: updatedFields };
    });
  };

  useEffect(() => {
    const atLeastOneFieldValid = fields.some(
      (field) => field.attributeName && field.dataType
    );
    const isLayerNameValid = formData.layerName.trim() !== "";
    console.log("isLayerNameValid", isLayerNameValid);
    setFieldCreateButton(atLeastOneFieldValid);
    if (isLayerNameValid && atLeastOneFieldValid) {
      setCreateButton(true);
    } else {
      setCreateButton(false);
    }
  }, [fields, formData]);

  const handleSubmit = (event) => {
    event.preventDefault();
    const project_id = Cookies.get("project_id");
    formData.project_id = project_id;
    if (project_id === undefined || project_id === "null") {
      console.log("project_id is not set..............");
      setAlertMessage("select a project to add layer to");
      setAlertStatus(false);
      setAlertOpen(true);
      console.log("project_id is not set");
      return;
    }
    console.log(formData);
    CreateLayer(formData)
      .then((response) => {
        console.log(response);
        setAlertMessage(response.data.message);
        setAlertStatus(response.data.status);
        setAlertOpen(true);        
      })
      .catch((error) => {
        console.error("Error submitting form:", error);
        setAlertMessage(error.message);
        setAlertStatus(false);
        setAlertOpen(true);
      });
    setFieldCreateButton(false);
    setCreateButton(false);
    handleClose();
  };

  return (
    <React.Fragment>
      <Dialog
        open={openNewLayer}
        onClose={handleClose}
        PaperProps={{
          component: "form",
          onSubmit: (event) => {
            event.preventDefault();
            const formData = new FormData(event.currentTarget);
            const formJson = Object.fromEntries(formData.entries());
            const email = formJson.email;
            console.log(email);
            handleClose();
          },
          style: { width: "100%", color: "#1976d2" },
        }}
      >
        <DialogTitle>Add New Layer</DialogTitle>
        <DialogContent>
          <div style={{ display: "flex" }}>
            <FormControl sx={{ m: 1, width: "50%", pr: 1 }}>
              <FormLabel id="layer_name" sx={{ color: "#1976d2" }}>
                Layer Name
              </FormLabel>
              <TextField
                aria-labelledby="layer_name"
                id="layer_name"
                name="layerName"
                variant="outlined"
                value={formData.layerName}
                onChange={handleChange}
              />
            </FormControl>
            <FormGroup sx={{ m: 1, width: "50%", pl: 1 }}>
              <FormLabel
                id="demo-radio-buttons-group-label"
                sx={{ color: "#1976d2" }}
              >
                Layer Type
              </FormLabel>
              <RadioGroup
                row
                aria-labelledby="demo-row-radio-buttons-group-label"
                name="layerType"
                color="primary"
                value={formData.layerType}
                onChange={handleChange}
              >
                <FormControlLabel
                  value="Point"
                  control={<Radio size="small" sx={{ color: "#1976d2" }} />}
                  label="point"
                />
                <FormControlLabel
                  value="LineString"
                  control={<Radio size="small" sx={{ color: "#1976d2" }} />}
                  label="line"
                />
                <FormControlLabel
                  value="Polygon"
                  control={<Radio size="small" sx={{ color: "#1976d2" }} />}
                  label="polygon"
                />
              </RadioGroup>
            </FormGroup>
          </div>
          {fields.map((field, index) => (
            <div
              key={index}
              style={{ display: "flex", alignItems: "flex-end", mt: 2 }}
            >
              <FormControl sx={{ m: 1, width: "50%", pr: 1 }}>
                <FormLabel id="attribute_name" sx={{ color: "#1976d2" }}>
                  Attribute Name
                </FormLabel>
                <TextField
                  aria-labelledby="attribute_name"
                  id="attribute_name"
                  name="attributeName"
                  variant="outlined"
                  value={field.attributeName}
                  onChange={(event) =>
                    handleFieldChange(
                      index,
                      "attributeName",
                      event.target.value
                    )
                  }
                />
              </FormControl>
              <FormControl sx={{ m: 1, width: "50%" }}>
                <FormLabel id="select_data_type" sx={{ color: "#1976d2" }}>
                  Data types
                </FormLabel>
                <Select
                  labelId="select_data_type"
                  id="demo-simple-select-helper"
                  name="dataType"
                  value={field.dataType}
                  onChange={(event) =>
                    handleFieldChange(index, "dataType", event.target.value)
                  }
                >
                  <MenuItem value="">
                    <em>None</em>
                  </MenuItem>
                  {dataTypes.map((type, index) => (
                    <MenuItem key={index} value={type}>
                      {type}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </div>
          ))}
          <div style={{ m: 2, width: "100%" }}>
            <ListItemButton
              onClick={handleAddField}
              disabled={!fieldCreateButton}
            >
              <ListItemIcon>
                <AddIcon sx={{ color: "#1976d2" }} />
              </ListItemIcon>
              <ListItemText primary="Add Attribute" />
            </ListItemButton>
          </div>
        </DialogContent>         
        <DialogActions sx={{ m: 2 }}>
          <Button
            variant="contained"
            onClick={handleSubmit}
            disabled={!createButton}
          >
            Create
          </Button>
          <Button onClick={handleClose}>Cancel</Button>
        </DialogActions>
      </Dialog>
      {alertOpen ? (
        <AlertComponent
          alertOpen={alertOpen}
          setAlertOpen={setAlertOpen}
          message={alertMessage}
          alertStatus={alertStatus}
        />
      ) : null}
    </React.Fragment>
  );
};

export default AddNewLayerModal;
