import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import AlertComponent from "../Alerts/AlertComponent";
import Button from "@mui/material/Button";
import DialogContent from "@mui/material/DialogContent";
import DialogActions from "@mui/material/DialogActions";
import IconButton from "@mui/material/IconButton";
import CloseIcon from "@mui/icons-material/Close";
import Typography from "@mui/material/Typography";
import Paper from "@mui/material/Paper";
import TextField from "@mui/material/TextField";
import Box from "@mui/material/Box";
import Select from "@mui/material/Select";
import MenuItem from "@mui/material/MenuItem";
import InputLabel from "@mui/material/InputLabel";
import FormControl from "@mui/material/FormControl";
import { CreateProject } from "../../api/Axios";

const projections = ["SLD 99", "ISO 2000"];
const layers = ["OpenStreetMap", "Images", "Contours"];
const privacys = ["Public", "Private"];
const deployments = ["Web", "Mobile"];
const databases = ["PostgreSQL", "MongoDB"];

export default function AddNewProject() {
  const navigate = useNavigate();
  const [formData, setFormData] = useState({
    projectName: "",
    layers: "",
    projection: "",
    deployments: "",
    privacy: "",
    database_name: "",
  });
  const [alertOpen, setAlertOpen] = useState(false);
  const [alertMessage, setAlertMessage] = useState("");
  const [alertStatus, setAlertStatus] = useState("");

  const handleClose = () => {
    setFormData({
      projectName: "",
      layers: "",
      projection: "",
      deployments: "",
      privacy: "",
      database_name: "",
    });
    navigate("/recent-projects");
  };

  const handleChange = (event) => {
    const { name, value } = event.target;
    setFormData((prevFormData) => ({ ...prevFormData, [name]: value }));
    console.log("formData", formData);
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    console.log(formData);
    CreateProject(formData)
      .then((response) => {
        console.log(response.data);
        setAlertMessage(response.data.message);
        setAlertStatus(response.data.status);
        setAlertOpen(true);
      })
      .catch((error) => {
        setAlertMessage(error.message);
        setAlertStatus(false);
        setAlertOpen(true);
      });    
  };

  return (
    <React.Fragment>
      <Paper
        sx={{ margin: "110px 20px 20px 20px", height: "100%", width: "50%" }}
      >
        <div
          style={{
            display: "flex",
            justifyContent: "space-between",
            alignItems: "flex-start",
          }}
        >
          <Typography
            variant="h6"
            sx={{ m: 0, p: 2 }}
            id="customized-dialog-title"
          >
            Project
          </Typography>
          <IconButton
            aria-label="close"
            sx={{
              // position: "absolute",
              right: 8,
              top: 8,
              color: (theme) => theme.palette.grey[1000],
            }}
            onClick={handleClose}
          >
            <CloseIcon />
          </IconButton>
        </div>
        <DialogContent dividers>
          <Box
            sx={{
              //   width: 500,
              maxWidth: "100%",
            }}
          >
            <TextField
              sx={{ margin: "1%" }}
              fullWidth
              label="Project Name"
              id="project-name"
              name="projectName"
              value={formData.projectName}
              onChange={handleChange}
            />
            <FormControl sx={{ margin: "1%" }} fullWidth>
              <InputLabel id="select-layers">Layers</InputLabel>
              <Select
                labelId="select-layers"
                id="select-layers"
                name="layers"
                label="layers"
                value={formData.layers}
                onChange={handleChange}
              >
                {layers.map((layer) => (
                  <MenuItem key={layer} value={layer}>
                    {layer}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
            <FormControl sx={{ margin: "1%" }} fullWidth>
              <InputLabel id="select-projection">Projection</InputLabel>
              <Select
                labelId="select-projection"
                id="select-projection"
                name="projection"
                label="Projection"
                value={formData.projection}
                onChange={handleChange}
              >
                {projections.map((projection) => (
                  <MenuItem key={projection} value={projection}>
                    {projection}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
            <FormControl sx={{ margin: "1%" }} fullWidth>
              <InputLabel id="select-deployments">Deployments</InputLabel>
              <Select
                labelId="select-deployments"
                id="select-deployments"
                name="deployments"
                label="deployments"
                value={formData.deployments}
                onChange={handleChange}
              >
                {deployments.map((deployment) => (
                  <MenuItem key={deployment} value={deployment}>
                    {deployment}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
            <FormControl sx={{ margin: "1%" }} fullWidth>
              <InputLabel id="select-privacy">Privacy</InputLabel>
              <Select
                labelId="select-privacy"
                id="select-privacy"
                name="privacy"
                label="Privacy"
                value={formData.privacy}
                onChange={handleChange}
              >
                {privacys.map((privacy) => (
                  <MenuItem key={privacy} value={privacy}>
                    {privacy}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
            <FormControl sx={{ margin: "1%" }} fullWidth>
              <InputLabel id="select-database">Database</InputLabel>
              <Select
                labelId="select-database"
                id="select-database"
                name="database_name"
                label="database"
                value={formData.database_name}
                onChange={handleChange}
              >
                {databases.map((database) => (
                  <MenuItem key={database} value={database}>
                    {database}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          </Box>
        </DialogContent>
        <DialogActions>
          <Button variant="contained" onClick={handleSubmit}>
            Create
          </Button>
        </DialogActions>
      </Paper>
      {alertOpen ? (
        <AlertComponent
          alertOpen={alertOpen}
          setAlertOpen={setAlertOpen}
          message={alertMessage}
          alertStatus={alertStatus}
        />
      ) : null}
    </React.Fragment>
  );
}
